import { Box, Button, Chip, Container, InputAdornment, Menu, MenuItem, Stack, TextField, Typography } from '@mui/material';
import { Helmet } from 'react-helmet-async';
import { useSettingsContext } from 'src/components/settings';
import { color, decryptToken, formatDate, formatObj, shortcutsItems } from 'src/utils/common';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { useState, useEffect, useDebugValue } from 'react';
import { constants } from 'src/utils/constant';
import { useLocation } from 'react-router';
import axios, { endpoints } from 'src/utils/axios';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import SearchIcon from '@mui/icons-material/Search';
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs/custom-breadcrumbs';
import { paths } from 'src/routes/paths';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DateRangePicker } from '@mui/x-date-pickers-pro/DateRangePicker';
import { SingleInputDateRangeField } from '@mui/x-date-pickers-pro/SingleInputDateRangeField';
import dayjs from 'dayjs';
import { DatePicker } from '@mui/x-date-pickers';
import * as XLSX from 'xlsx';
import JsPDF from 'jspdf';
import 'jspdf-autotable';
import { saveAs } from 'file-saver';
// sections

// ----------------------------------------------------------------------

export default function LedgerDetails() {
    const settings = useSettingsContext();
    const [isLoading, setIsLoading] = useState(false)
    const [list, setList] = useState([]);
    const [tempData, setTempData] = useState([]);
    const [searchQuery, setSearchQuery] = useState('');
    const { enqueueSnackbar } = useSnackbar();
    const location = useLocation();
    const rowData = location?.state;

    const [selectedDateRange, setSelectedDateRange] = useState([dayjs().startOf('year'), dayjs().endOf('year')]);
    // Function to handle date range change
    const handleDateRangeChange = (newValue) => {
        setSelectedDateRange(newValue);
    };

    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);

    const fetchData = async (startDate, endDate) => {
        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.reports.ledgerDetails, { account: rowData.account, startDate, endDate }, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setIsLoading(false);
                setList(response.data.data);
                setTempData(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch sales orders!', { variant: 'error' });
            setIsLoading(false);
        }
    };
    const [startDate, setStartDate] = useState(dayjs().startOf('month'));
    const [endDate, setEndDate] = useState(dayjs().endOf('month'));

    const handleStartDateChange = (date) => {
        setStartDate(date);
    };

    const handleEndDateChange = (date) => {
        setEndDate(date);
    };

    useEffect(() => {
        setIsLoading(true);
        fetchData(startDate.toDate(), endDate.toDate());

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [startDate, endDate]);





    const columns = [
        {
            name: 'Transaction Date',
            selector: row => new Date(row.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
            sortable: 'true',
        },
        {
            name: 'Description',
            selector: row => row.description || '-',
            center: 'true'
        },
        {
            name: 'Payment Mode',
            selector: row => row.paymentMode,
            sortable: 'true',
        },
        {
            name: 'Invoice/Cheque No',
            selector: row => row.paymentMode === 'CHEQUE' ? (row.chequeNumber || row.invoiceNumber) : row.invoiceNumber,
            sortable: 'true',
        },
        {
            name: 'Debit',
            selector: row => {
                if (row.partyType === 'VENDOR' || row.partyType === 'EXPENSE') {
                    return row.amount.toFixed(2);
                }
                return 0;
            },
            sortable: true
        },
        {
            name: 'Credit',
            selector: row => {
                if (row.partyType === 'CUSTOMER' || row.partyType === 'INCOME') {
                    return row.amount.toFixed(2);
                };
                return 0;
            },
            sortable: 'true',
        },
        {
            name: 'Balance',
            selector: row => row.balance.toFixed(2),
            sortable: 'true',
        },
    ];

    const handleDownloadPDF = () => {
        setAnchorEl(null);
        const doc = new JsPDF();
        doc.text(`${rowData.account} Ledger Report`, 10, 10);

        // Get table data
        // Transform data and create tableData array
        const tableData = filteredSales.map(item => [
            new Date(item.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
            item.description || '-',
            item.paymentMode,
            item.paymentMode === 'CHEQUE' ? (item.chequeNumber || item.invoiceNumber) : item.invoiceNumber,
            (item.partyType === 'VENDOR' || item.partyType === 'EXPENSE') ? item.amount.toFixed(2) : 0,
            (item.partyType === 'CUSTOMER' || item.partyType === 'INCOME') ? item.amount.toFixed(2) : 0,
            item.balance.toFixed(2),
        ]);

        // Set column headers
        const headers = ['Transaction Date', 'Description', 'Payment Mode', 'Invoice/Cheque No', 'Debit', 'Credit', 'Balance'];

        // Add table to PDF
        doc.autoTable({
            head: [headers],
            body: tableData,
        });

        doc.save(`${rowData.account}_ledger_report.pdf`);
    };

    const handleDownloadExcel = () => {
        setAnchorEl(null);
        // Convert data to a format suitable for Excel
        const excelData = filteredSales.map(item => ({
            'Transaction Date': new Date(item.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
            'Description': item.description || '-',
            'Payment Mode': item.paymentMode,
            'Invoice/Cheque No': item.paymentMode === 'CHEQUE' ? (item.chequeNumber || item.invoiceNumber) : item.invoiceNumber,
            'Debit': (item.partyType === 'VENDOR' || item.partyType === 'EXPENSE') ? item.amount.toFixed(2) : 0,
            'Credit': (item.partyType === 'CUSTOMER' || item.partyType === 'INCOME') ? item.amount.toFixed(2) : 0,
            'Balance': item.balance.toFixed(2),
        }));

        // Create a new workbook
        const workbook = XLSX.utils.book_new();
        // Convert the data to a worksheet
        const worksheet = XLSX.utils.json_to_sheet(excelData);
        // Add the worksheet to the workbook
        XLSX.utils.book_append_sheet(workbook, worksheet, `${rowData.account}_Ledger Report`);

        // Convert the workbook to a binary Excel file
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        // Create a Blob from the buffer
        const data = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8' });
        // Save the Blob as a file using file-saver
        saveAs(data, `${rowData.account}_ledger_report.xlsx`);
    };


    const filteredSales = list?.filter((data) =>
        data?.invoiceNumber?.toLowerCase().includes(searchQuery?.toLowerCase())
        ||
        (data.chequeNumber && data?.chequeNumber?.toString().toLowerCase().includes(searchQuery?.toLowerCase()))
    );

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Helmet>
                <title> Reports: Ledger Details</title>
            </Helmet>

            <CustomBreadcrumbs
                heading='Ledger Details'
                links={[
                    {
                        name: 'Ledger',
                        href: paths.dashboard.reports.ledger,
                    },
                    {
                        name: `${rowData.account}`,
                    },
                ]}
                sx={{
                    mb: { xs: 3, md: 5 },
                }}
            />
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between", marginTop: "20px" }}>
                <Stack direction="row" spacing={1}>
                    <Chip
                        label={`Opening Balance : ${filteredSales[filteredSales.length - 1]?.balance ? formatObj.format(filteredSales[filteredSales.length - 1].balance) : 0}`}
                        variant="outlined"
                    />
                    <Chip
                        label={`Closing Balance : ${filteredSales[0]?.balance ? formatObj.format(filteredSales[0].balance) : 0}`}
                        variant="outlined"
                    />
                </Stack>

                <Stack>
                    <Button style={{ color: color.actionButton }} endIcon={<ArrowDropDownIcon />} variant='outlined' onClick={(e) => setAnchorEl(e.currentTarget)}>
                        ACTIONS
                    </Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >

                        <MenuItem onClick={() => {
                        }}>Print</MenuItem>
                        <MenuItem onClick={handleDownloadPDF}>Download as PDF</MenuItem>
                        <MenuItem onClick={handleDownloadExcel}>Download as Excel</MenuItem>

                    </Menu>
                </Stack>
            </div>

            <div style={{ display: "flex", gap: '10px', marginTop: "20px" }}>
                <TextField
                    placeholder='Search'
                    size='small'
                    value={searchQuery}
                    onChange={e => setSearchQuery(e.target.value)}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
                {/*  Wan to build a daterange picker from mui and when i change the dates the api should called  */}
                {/* <LocalizationProvider dateAdapter={AdapterDayjs}>
                    <DateRangePicker
                        slotProps={{
                            textField: { size: 'small' }, shortcuts: {
                                items: shortcutsItems,
                            },
                        }}
                        value={selectedDateRange}
                        onChange={handleDateRangeChange}
                        slots={{ field: SingleInputDateRangeField }}
                    />
                </LocalizationProvider> */}

                <div>
                    <LocalizationProvider dateAdapter={AdapterDayjs} >
                        <Box sx={{ display: 'flex', flexDirection: 'row', gap: '10px', alignItems: 'center' }}>
                            <DatePicker
                                label="From"
                                value={startDate}
                                slotProps={{ textField: { size: 'small' } }}
                                onChange={handleStartDateChange}
                                format='DD/MM/YYYY'
                                renderInput={(props) => <TextField {...props} />}
                            />
                            <Typography>-</Typography>
                            <DatePicker
                                label="To"
                                value={endDate}
                                format='DD/MM/YYYY'
                                slotProps={{ textField: { size: 'small' } }}
                                onChange={handleEndDateChange}
                                renderInput={(props) => <TextField {...props} />}
                            />
                        </Box>
                    </LocalizationProvider>
                </div>
            </div>
            <div style={{ marginTop: "20px" }}>
                <DataTable
                    columns={columns}
                    data={filteredSales}
                    pagination
                    progressPending={isLoading}
                />
            </div>
        </Container>
    );
}
